import React from 'react';
import { List, Counter, Placeholder, Avatar, Cell, Card, CardGrid, Group, Div, Panel, PanelHeader, PanelHeaderButton, platform, IOS } from '@vkontakte/vkui';

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline';
import Icon28ServicesOutline from '@vkontakte/icons/dist/28/services_outline';


const osname = platform();

const Info = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            About
        </PanelHeader>


      

        <Group>

        <CardGrid>
          <Card size="l" mode="shadow">
          <Placeholder
            icon={<Avatar size={150} mode="image" src="https://sun1-26.userapi.com/MRDTjpaFaSdMjQ0xrJC6fBNUte_6k-MZygsWEQ/5ODGD7Jo6yc.jpg" />}
            header="Моё Далматово"
            
          >Моё Далматово
          <br/>
          Первое городское приложение.
          <br/>
          Всё для вас!
            
          </Placeholder>
            <div style={{ height: 15 }} />
          </Card>
        </CardGrid>


        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
          <Cell before={<Icon28ArticleOutline />} indicator={<Counter mode="primary">1.7</Counter>}>Версия приложения:</Cell>
          <Cell before={<Icon28BugOutline />} indicator={<Counter mode="primary">Beta</Counter>}>Стадия разработки:</Cell>
          <Cell before={<Icon28ServicesOutline />} indicator={<Counter>4 дня</Counter>}>До закрытия Eruda:</Cell>

          </List>
            <div style={{ height: 5 }} />
          </Card>
        </CardGrid>


        </Group>
    </Panel>
);

export default Info;