import React from 'react';
import {  Cell, Switch, platform, IOS, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

// иконки


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //iso
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();

const Edit = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
         Настройки
        </PanelHeader>
        <Group>
        <Cell asideContent={<Switch  />}>
            Тёмная тема
          </Cell>
    </Group>
    </Panel>
);

export default Edit;