import React from 'react';
import { ModalRoot, ModalCard, Counter, Group, Cell, Separator, Avatar, CardGrid, Card, Placeholder, platform, IOS, Panel, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';




//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


import Icon28InfoOutline from '@vkontakte/icons/dist/28/info_outline';


const osname = platform();

const Weather = props => (
<Panel id={props.id}>
<PanelHeader
left={<PanelHeaderButton onClick={props.go} data-to="home">
{osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
</PanelHeaderButton>}
>
Далматово
</PanelHeader>





<CardGrid>
<Card size="l" mode="outline">
<Placeholder
icon={<Avatar size={64} mode="image" src="https://sun1-27.userapi.com/kb0UnQqfhH_PWkPZQFWdwf5B7uh6TLjgLXpTcA/r5OvjFL0a6M.jpg" />}
header="Суббота, 18"

>Температура днём: +12
<br/>
Температура ночью: +1
<br/>
Вероятность дождя: 10%
<br/>
Влажность: 47%
<br/>
Давление: 753,36 мм рт. ст.

</Placeholder>
<div style={{ height: 1 }} />
</Card>
</CardGrid>






<Group>
<CardGrid>
<Card size="l" mode="outline">
<Cell before={<Avatar size={45} mode="image" src="https://sun1-21.userapi.com/t_MIN3Q8hWGHf2N__7eEjOtC1tPxI-DXTQ54gQ/bBuTszaKuek.jpg" />} indicator={<Counter>3-14°</Counter>}>19 Апреля</Cell>
<div style={{ height: 1 }} />
</Card>
</CardGrid>

<CardGrid>
<Card size="l" mode="outline">
<Cell before={<Avatar size={45} mode="image" src="https://sun1-27.userapi.com/kb0UnQqfhH_PWkPZQFWdwf5B7uh6TLjgLXpTcA/r5OvjFL0a6M.jpg" />} indicator={<Counter>4-13°</Counter>}>20 Апреля</Cell>
<div style={{ height: 1 }} />
</Card>
</CardGrid>

<CardGrid>
<Card size="l" mode="outline">
<Cell before={<Avatar size={45} mode="image" src="https://sun1-95.userapi.com/WSTuXoQWfJWEuIOK0wzrFC8S8Sm9c9FB4FcDPA/-F7m8EcEpLA.jpg" />} indicator={<Counter>9-17°</Counter>}>21 Апреля</Cell>
<div style={{ height: 1 }} />
</Card>
</CardGrid>

<CardGrid>
<Card size="l" mode="outline">
<Cell before={<Avatar size={45} mode="image" src="https://sun1-27.userapi.com/kb0UnQqfhH_PWkPZQFWdwf5B7uh6TLjgLXpTcA/r5OvjFL0a6M.jpg" />} indicator={<Counter>5-19°</Counter>}>22 Апреля</Cell>
<div style={{ height: 1 }} />
</Card>
</CardGrid>
</Group>





<Separator style={{ margin: '12px 0' }} />
<Cell before={<Icon28InfoOutline />}>Представлено Weather.com</Cell>
</Panel>

);

export default Weather;