import React from 'react';
import {  Avatar, Counter, Header, platform, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';


//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android


import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline';
import Icon28LogoInstagram from '@vkontakte/icons/dist/28/logo_instagram';
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline';
import Icon28ServicesOutline from '@vkontakte/icons/dist/28/services_outline';




const osname = platform();

const Developer = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="home">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Разработчики
        </PanelHeader>
       <Group>
       

       {<Header mode="secondary">Разработчик</Header>}
            <Cell before={<Avatar size={50} mode="image" src="https://sun1-96.userapi.com/iUuYKGmZrzUxF0BSYmkY_nTaq5H-KfDvYc0DEA/95RqyS1Ffqs.jpg" />}>Егор Гордеев</Cell>
            <Cell before={<Avatar size={50} mode="image" src="https://sun1-98.userapi.com/G6CxVgE7TgMLe7NdNVdWykhJb72Y_ZvEpEhjmg/UbrxpMtCAnM.jpg" />}>_gordeev_egor_</Cell>
            <Cell before={<Avatar size={50} mode="image" src="https://sun9-36.userapi.com/c858328/v858328488/1842f1/ujqH0jJIRwg.jpg" />}>Dalmatovo 360</Cell>



       


    </Group>

    </Panel>
);

export default Developer;