
import React from 'react';

import {  Avatar, Div, Header, Counter, Card, CardGrid, platform, List, IOS, Cell, Panel, Group, PanelHeader, PanelHeaderButton } from '@vkontakte/vkui';

//иконки назад
import Icon28ArrowLeftOutline from '@vkontakte/icons/dist/28/arrow_left_outline'; //ios
import Icon24Back from '@vkontakte/icons/dist/24/back'; //android

const osname = platform();
const Covid = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton onClick={props.go} data-to="covidmenu">
        {osname === IOS ? <Icon28ArrowLeftOutline/> : <Icon24Back/>}
    </PanelHeaderButton>}
        >
            Коронавирус
        </PanelHeader>
        <Group> 
        <CardGrid>
          <Card size="l" mode="shadow">
          <List>
       <Cell before={<Avatar size={28} mode="image" src="https://sun1-23.userapi.com/YZNJNpbIeEVuBpEPdkrbKMpNGnmhTrxLPLovnw/rSKlCwHEF-0.jpg" />} indicator={<Counter mode="primary">3</Counter>}>Выздоровело:</Cell>
       <Cell before={<Avatar size={28} mode="image" src="https://sun9-26.userapi.com/c857736/v857736115/1cc8af/_V7V3jO-sj4.jpg" />}  indicator={<Counter mode="primary">10</Counter>}>Заражено:</Cell>
       <Cell before={<Avatar size={28} mode="image" src="https://sun1-30.userapi.com/pME0o0gSVhF5cA2AmBKOicUPW0yA_Imz794WXQ/S8J8BT4OjKs.jpg" />}  indicator={<Counter>0</Counter>}>Умерло:</Cell>
       <Cell before={<Avatar size={28} mode="image" src="https://sun1-28.userapi.com/dV8-DvAWfasJ-kyX129bGR0_t_I1AQ_BvDctoQ/pAamsaQq9ZE.jpg" />}  indicator={<Counter mode="primary">11:25</Counter>}>Последнее обновление:</Cell>
     </List>
            <div style={{ height: 5 }} />
          </Card>
        </CardGrid>
        <CardGrid>
          <Card size="l" mode="outline">
          {<Header mode="secondary">Дополнительно</Header>}
          <Div style={{ paddingTop: 5, paddingBottom: 5, color: 'black' }}>
            Что означает данная информация?
            <br/>
            <br/>
            Первое поле: количество выписанных за весь период людей.
            <br/>
            <br/>
            Второе поле: количество людей, которые находятся на лечении.
            <br/>
            <br/>
            Третье поле: количество смертей за весь период.
            <br/>
            <br/>
            Четвёртое поле: время последнего обновления информации.
            </Div>
            <div style={{ height: 25 }} />
          </Card>
        </CardGrid>
    </Group>
    </Panel>
);

export default Covid;