import React from 'react';
import { Avatar, Button, Header, PanelHeaderButton, IOS, platform, Card, CardGrid, Cell, List, Panel, PanelHeader, Group } from '@vkontakte/vkui';

//иконки
import Icon28BugOutline from '@vkontakte/icons/dist/28/bug_outline'; // Коронавирус
import Icon28ArticleOutline from '@vkontakte/icons/dist/28/article_outline'; // О приложении
import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline'; // Настройки
import Icon28TargetOutline from '@vkontakte/icons/dist/28/target_outline'; // Организации
import Icon56PlaceOutline from '@vkontakte/icons/dist/56/place_outline'; // Погода

//Иконки для ОС
import Icon28LogoVkOutline from '@vkontakte/icons/dist/28/logo_vk_outline';
import Icon24LogoVk from '@vkontakte/icons/dist/24/logo_vk';



const osname = platform();

const Home = props => (
    <Panel id={props.id}>
        <PanelHeader
        left={<PanelHeaderButton>
        {osname === IOS ? <Icon28LogoVkOutline/> : <Icon24LogoVk/>}
    </PanelHeaderButton>}
        >
         Моё Далматово
        </PanelHeader>
        <Group>
        <CardGrid>
<Card size="l" mode="shadow">
<List>
<Cell expandable before={<Icon28BugOutline />} onClick={props.go} data-to="covidmenu">Коронавирус</Cell>
<Cell expandable before={<Icon28ArticleOutline />} onClick={props.go} data-to="info">О приложении</Cell>
<Cell expandable before={<Icon28TargetOutline />} onClick={props.go} data-to="organizations">Организации города</Cell>
<Cell expandable before={<Icon56PlaceOutline width={30} height={30} />} onClick={props.go} data-to="weather">Погода</Cell>
<Cell expandable before={<Icon28SettingsOutline />}>Настройки</Cell>
</List>
<div style={{ height: 10 }} />
</Card>
</CardGrid>

    </Group>

    {<Header mode="secondary">Разработчик</Header>}
<Cell
description="VKontakte"
bottomContent={<Button onClick={props.go} data-to="developer">Подробнее</Button>}
before={<Avatar size={65} mode="image" src="https://sun1-96.userapi.com/iUuYKGmZrzUxF0BSYmkY_nTaq5H-KfDvYc0DEA/95RqyS1Ffqs.jpg" />}
size="l"
>
Егор Гордеев
</Cell>

    </Panel>
);

export default Home;

